import { cva, type VariantProps } from 'class-variance-authority';
import { forwardRef } from 'react';
import { Slot } from '@radix-ui/react-slot';

// ============= BUTTON =============
const buttonVariants = cva(
    'inline-flex items-center justify-center gap-2 whitespace-nowrap rounded-xl text-sm font-semibold transition-all duration-200 focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-blue-500 focus-visible:ring-offset-2 focus-visible:ring-offset-black disabled:pointer-events-none disabled:opacity-50',
    {
        variants: {
            variant: {
                default: 'bg-gradient-to-r from-blue-600 to-blue-500 text-white shadow-lg shadow-blue-500/25 hover:shadow-blue-500/40 hover:brightness-110',
                secondary: 'bg-white/10 text-white hover:bg-white/20 border border-white/10',
                outline: 'border border-white/20 bg-transparent text-white hover:bg-white/10 hover:border-white/30',
                ghost: 'text-zinc-400 hover:text-white hover:bg-white/10',
                destructive: 'bg-red-600 text-white hover:bg-red-500 shadow-lg shadow-red-500/25',
                success: 'bg-gradient-to-r from-emerald-600 to-emerald-500 text-white shadow-lg shadow-emerald-500/25',
            },
            size: {
                default: 'h-11 px-6',
                sm: 'h-9 px-4 text-xs',
                lg: 'h-14 px-8 text-base',
                icon: 'h-10 w-10',
            },
        },
        defaultVariants: {
            variant: 'default',
            size: 'default',
        },
    }
);

export interface ButtonProps
    extends React.ButtonHTMLAttributes<HTMLButtonElement>,
    VariantProps<typeof buttonVariants> {
    asChild?: boolean;
}

export const Button = forwardRef<HTMLButtonElement, ButtonProps>(
    ({ className, variant, size, asChild = false, ...props }, ref) => {
        const Comp = asChild ? Slot : 'button';
        return (
            <Comp
                className={buttonVariants({ variant, size, className })}
                ref={ref}
                {...props}
            />
        );
    }
);
Button.displayName = 'Button';

// ============= CARD =============
export function Card({
    children,
    className = '',
    hover = false,
    glow = false,
}: {
    children: React.ReactNode;
    className?: string;
    hover?: boolean;
    glow?: boolean;
}) {
    return (
        <div
            className={`
        glass-card rounded-2xl p-6
        ${hover ? 'transition-all duration-300 hover:border-white/20 hover:shadow-xl hover:-translate-y-1' : ''}
        ${glow ? 'glow-blue' : ''}
        ${className}
      `}
        >
            {children}
        </div>
    );
}

// ============= BADGE =============
const badgeVariants = cva(
    'inline-flex items-center gap-1.5 rounded-full px-3 py-1 text-xs font-semibold transition-colors',
    {
        variants: {
            variant: {
                default: 'bg-zinc-800 text-zinc-300 border border-zinc-700',
                primary: 'bg-blue-500/20 text-blue-400 border border-blue-500/30',
                success: 'bg-emerald-500/20 text-emerald-400 border border-emerald-500/30',
                warning: 'bg-amber-500/20 text-amber-400 border border-amber-500/30',
                destructive: 'bg-red-500/20 text-red-400 border border-red-500/30',
                purple: 'bg-purple-500/20 text-purple-400 border border-purple-500/30',
            },
        },
        defaultVariants: {
            variant: 'default',
        },
    }
);

interface BadgeProps extends VariantProps<typeof badgeVariants> {
    children: React.ReactNode;
    className?: string;
    dot?: boolean;
}

export function Badge({ children, variant, className, dot }: BadgeProps) {
    return (
        <span className={badgeVariants({ variant, className })}>
            {dot && (
                <span className={`w-1.5 h-1.5 rounded-full ${variant === 'success' ? 'bg-emerald-400' :
                        variant === 'warning' ? 'bg-amber-400' :
                            variant === 'destructive' ? 'bg-red-400' :
                                variant === 'primary' ? 'bg-blue-400' :
                                    variant === 'purple' ? 'bg-purple-400' :
                                        'bg-zinc-400'
                    } animate-pulse`} />
            )}
            {children}
        </span>
    );
}

// ============= INPUT =============
export const Input = forwardRef<
    HTMLInputElement,
    React.InputHTMLAttributes<HTMLInputElement> & { icon?: React.ReactNode }
>(({ className, icon, ...props }, ref) => {
    return (
        <div className="relative">
            {icon && (
                <div className="absolute left-4 top-1/2 -translate-y-1/2 text-zinc-500">
                    {icon}
                </div>
            )}
            <input
                className={`
          flex h-12 w-full rounded-xl border border-white/10 bg-white/5 
          px-4 py-3 text-sm text-white placeholder:text-zinc-500
          focus:outline-none focus:ring-2 focus:ring-blue-500/50 focus:border-blue-500/50
          disabled:cursor-not-allowed disabled:opacity-50
          transition-all duration-200
          ${icon ? 'pl-12' : ''}
          ${className}
        `}
                ref={ref}
                {...props}
            />
        </div>
    );
});
Input.displayName = 'Input';

// ============= SELECT =============
export const Select = forwardRef<
    HTMLSelectElement,
    React.SelectHTMLAttributes<HTMLSelectElement> & { icon?: React.ReactNode }
>(({ className, icon, children, ...props }, ref) => {
    return (
        <div className="relative">
            {icon && (
                <div className="absolute left-4 top-1/2 -translate-y-1/2 text-zinc-500 pointer-events-none z-10">
                    {icon}
                </div>
            )}
            <select
                className={`
          flex h-12 w-full rounded-xl border border-white/10 bg-white/5 
          px-4 py-3 text-sm text-white
          focus:outline-none focus:ring-2 focus:ring-blue-500/50 focus:border-blue-500/50
          disabled:cursor-not-allowed disabled:opacity-50
          transition-all duration-200 appearance-none cursor-pointer
          ${icon ? 'pl-12' : ''}
          ${className}
        `}
                ref={ref}
                {...props}
            >
                {children}
            </select>
            <div className="absolute right-4 top-1/2 -translate-y-1/2 text-zinc-500 pointer-events-none">
                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
                </svg>
            </div>
        </div>
    );
});
Select.displayName = 'Select';

// ============= STAT CARD =============
export function StatCard({
    title,
    value,
    change,
    icon: Icon,
    trend,
}: {
    title: string;
    value: string | number;
    change?: string;
    icon?: React.ComponentType<{ className?: string }>;
    trend?: 'up' | 'down' | 'neutral';
}) {
    return (
        <Card hover className="relative overflow-hidden">
            <div className="absolute top-0 right-0 w-32 h-32 bg-gradient-to-br from-blue-500/10 to-purple-500/10 rounded-full blur-2xl -translate-y-1/2 translate-x-1/2" />
            <div className="relative">
                <div className="flex items-center justify-between mb-4">
                    <span className="text-sm font-medium text-zinc-400">{title}</span>
                    {Icon && (
                        <div className="w-10 h-10 rounded-xl bg-white/5 flex items-center justify-center">
                            <Icon className="w-5 h-5 text-zinc-400" />
                        </div>
                    )}
                </div>
                <div className="flex items-end gap-3">
                    <span className="text-3xl font-bold text-white">{value}</span>
                    {change && (
                        <span className={`text-sm font-medium ${trend === 'up' ? 'text-emerald-400' :
                                trend === 'down' ? 'text-red-400' :
                                    'text-zinc-400'
                            }`}>
                            {trend === 'up' && '↑'}
                            {trend === 'down' && '↓'}
                            {change}
                        </span>
                    )}
                </div>
            </div>
        </Card>
    );
}

// ============= PROGRESS =============
export function Progress({
    value,
    max = 100,
    size = 'default',
    showLabel = false,
}: {
    value: number;
    max?: number;
    size?: 'sm' | 'default' | 'lg';
    showLabel?: boolean;
}) {
    const percentage = Math.round((value / max) * 100);
    const heights = { sm: 'h-1', default: 'h-2', lg: 'h-3' };

    return (
        <div className="w-full">
            {showLabel && (
                <div className="flex justify-between text-sm mb-2">
                    <span className="text-zinc-400">Progress</span>
                    <span className="text-white font-medium">{percentage}%</span>
                </div>
            )}
            <div className={`w-full bg-white/10 rounded-full overflow-hidden ${heights[size]}`}>
                <div
                    className={`${heights[size]} bg-gradient-to-r from-blue-500 to-purple-500 rounded-full transition-all duration-500 ease-out`}
                    style={{ width: `${percentage}%` }}
                />
            </div>
        </div>
    );
}

// ============= SKELETON =============
export function Skeleton({ className }: { className?: string }) {
    return (
        <div className={`animate-shimmer bg-white/5 rounded-lg ${className}`} />
    );
}
